* {
    margin: 0;
    padding: 0;
}

.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color:white;
    height: 56px;
    box-shadow: -7px 3px 10px 0 rgba(0,0,0,.06);
    z-index: 2000;
}

.name {
    display: flex;
    width: 10%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.user {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 80%;
}