* {
    margin: 0;
    padding: 0;
}

.body {
    position: relative;
    height: 100%;
}

tr {
    margin-left: 10%;
    width: 80%;
    height: 50px;
    display: flex;
    flex-direction: row;
    border-top: 2px solid #f1f2f4;
    border-bottom: 2px solid #f1f2f4;
    border-spacing: 0;
    border-collapse: collapse;
    margin-top: -2px;
}

th:nth-child(1) {
    width: 70%;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #818999;
    padding-left: 5%;
    overflow: hidden;

}

th:nth-child(n+2) {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #818999;
    padding-left: 5%;
}


td:nth-child(1) {
    width: 70%;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #818999;
    padding-left: 5%;
    overflow: hidden;
}

td:nth-child(n+2) {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: blue;
    padding-left: 5%;
}

td span:hover:not(.close) {
    cursor: pointer;
    color: #417dff;
    text-decoration: underline;
}

td img {
    width: 32px;
    height: 32px;
}

.close {
    cursor: default;
}

* {
    margin: 0;
    padding: 0;
}

.header {
    margin-left: 10%;
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: left;
    height: 64px;
    z-index: 1;
}

form {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.header span {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.header input {
    display: none;
}

label {
    margin-right: 32px;
    color: white;
    background-color: #417dff;
    width: 100px;
    height: 32px;
    border-radius: 16px;
    font-size: 14px;
    font-weight: 700;
    border-color: #f0faff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header .button {
    margin-right: 32px;
    color: #417dff;
    background-color: #f0faff;
    width: 100px;
    height: 32px;
    border-radius: 16px;
    font-size: 14px;
    font-weight: 700;
    border-color: #f0faff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.body .togo {
    margin-left: 10%;
    display: flex;
    flex-direction: row;
    justify-content: left;
    height: 64px;
    align-items: center;
}

.left {
    width: 70%
}

.right .update {
    display: none;
}

.right .floder {
    display: none;
}

.right input {
    font-size: 16px;
    height: 32px;
    border: 0;
}

.right input:focus {
    outline: none;
    /* outline: 2px solid #417dff; */
    box-shadow: 0 0 4px #417dff;
    border-radius: 16px;
    /* 设置圆角的大小 */
}

.right button {
    margin-left: 32px;
    color: #417dff;
    background-color: #f0faff;
    width: 100px;
    height: 32px;
    border-radius: 16px;
    font-size: 14px;
    font-weight: 700;
    border-color: #f0faff;
    display: flex;
    justify-content: center;
    align-items: center;
}