* {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}


.big {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin: 0 auto;
}

h1 {
    margin: 0.2rem;
    font-size: 1.2rem;
}

p {
    margin: 1.2rem;
}

.container {
    position: relative;
    background-color: aliceblue;
    border-radius: 10px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
    /* padding: 0.6rem; */
    width: 50rem;
    height: 35rem;
    /* 溢出隐藏 */
    overflow: hidden;
    max-width: 100vw;
    min-height: 70vh;

}

.container .denglu {
    position: absolute;
    left: 0;
    width: 50%;
    height: 100%;
    z-index: 2;
}

.container .zhuce {
    position: absolute;
    left: 50%;
    width: 50%;
    height: 100%;
    z-index: 1;
}

.container .zhaohui {
    position: absolute;
    left: 50%;
    width: 50%;
    height: 100%;
    z-index: 0;
}

.container .denglu form {
    background: #fff;
    display: flex;
    flex-direction: column;
    padding: 0 1.8rem;
    height: 100%;
    justify-content: center;
    align-items: center;
}


.container .zhuce form {
    background: #fff;
    display: flex;
    flex-direction: column;
    padding: 0 1.8rem;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.container .zhaohui form {
    background: #fff;
    display: flex;
    flex-direction: column;
    padding: 0 1.8rem;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.container .tozhuce {
    position: absolute;
    left: 50%;
    width: 50%;
    height: 100%;
    z-index: 2;
    background: #417dff;
}

.container .todenglu {
    position: absolute;
    left: 0;
    width: 50%;
    height: 100%;
    z-index: 1;
    background: #417dff;
}

.container .todenglu1 {
    position: absolute;
    left: 0;
    width: 50%;
    height: 100%;
    z-index: 0;
    background: #417dff;
}



.display {
    display: flex;
    flex-direction: column;
    padding: 0 1.8rem;
    height: 100%;
    justify-content: center;
    align-items: center;
    color: #fff;
}


.big input {
    width: 100%;
    height: 2.2rem;
    text-indent: 1rem;
    border: 1px solid #ccc;
    /* 把input上左右边框取消 */
    border-left: none;
    border-right: none;
    border-top: none;
    /* 点击input边框消失 */
    outline: none;
    margin: 0.6rem 0;
}

button {
    padding: 0.4rem 1rem;
    background: #417dff;
    color: white;
    border: 1px solid #fff;
    outline: none;
    /* 鼠标放上变小手 */
    cursor: pointer;
    width: 5rem;
    border-radius: 8px;
    transition: all 100ms ease-in;
    margin: 0.6rem 0;
    font-size: 0.6rem;
    padding: 0.5rem 0;
}

.yzm {
    width: 100%;
}

.yzm1 {
    width: 100%;
}

#find {
    color: grey;
    font-size: smaller;
    text-decoration: underline;
    margin-top: 10px;
    cursor: pointer;
}

.tips{
    color: red;
    font-size: 6px;
    width: 100%;
    display: none;
}